<template>
  <el-row :gutter="10">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 30px;line-height: 150%;">
        Sponge is a powerful and easy-to-use Go development framework. Its core
        concept is to reverse-generate modular code by parsing SQL, Protobuf,
        and JSON files. These modular components can be flexibly combined into
        various types of complete backend services. Featuring a modular
        architecture and deeply integrated AI assistant, sponge significantly
        improves development efficiency and lowers the technical barrier,
        enabling the effortless construction of high-performance, highly
        available backend systems through a low-code approach.
      </p>
      <p>
        Github Repository:
        <a href="https://github.com/go-dev-frame/sponge" target="_blank"
          >https://github.com/go-dev-frame/sponge</a
        >
      </p>
      <p>
        Development Documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>
        <b>
          Sponge code generation engine.
        </b>
      </p>
      <p>
        <b>1.</b> The code generation framework based on built-in templates, as
        shown in the following figure:
      </p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>
        <b>2.</b> Sponge generates code framework based on custom templates, as
        shown in the following figure:
      </p>
      <div class="framework-image">
        <el-image :src="homeTplImageSrc"></el-image>
      </div>
      <br />
      <p>
        <b>3.</b> sponge generates a business logic code framework based on
        functions and comments, as shown in the following figure:
      </p>
      <div class="framework-image">
        <el-image :src="homeAssistantImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      homeTplImageSrc: require("@/assets/template-framework.png"),
      homeAssistantImageSrc: require("@/assets/ai-assistant-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.framework-image {
  width: 80%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



